/* Hero Section */
.hero {
    position: relative;
    height: 70vh;
    min-height: 500px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-slideshow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.slide-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    opacity: 0;
    transition: opacity 1s ease-in-out;
}

.slide-bg.active {
    opacity: 1;
}

.slide-bg:nth-child(1) {
    background-image: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)), url('https://images.unsplash.com/photo-1566073771259-6a8506099945?w=1920&h=1080&fit=crop');
}

.slide-bg:nth-child(2) {
    background-image: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)), url('https://images.unsplash.com/photo-1520250497591-112f2f40a3f4?w=1920&h=1080&fit=crop');
}

.slide-bg:nth-child(3) {
    background-image: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)), url('https://images.unsplash.com/photo-1445019980597-93fa8acb246c?w=1920&h=1080&fit=crop');
}

.slide-bg:nth-child(4) {
    background-image: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)), url('https://images.unsplash.com/photo-1502672260266-1c1ef2d93688?w=1920&h=1080&fit=crop');
}

.slide-bg:nth-child(5) {
    background-image: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)), url('https://images.unsplash.com/photo-1512917774080-9991f1c4c750?w=1920&h=1080&fit=crop');
}

.hero-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, rgba(46, 139, 87, 0.8), rgba(34, 139, 34, 0.6));
    z-index: 2;
}

.hero-content {
    position: relative;
    z-index: 3;
    text-align: center;
    color: var(--white-color);
    max-width: 800px;
    padding: 0 20px;
}

.hero-content h1 {
    font-size: 3.5rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
}

.hero-content p {
    font-size: 1.3rem;
    margin-bottom: 2.5rem;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
}

/* Search Bar */
.search-bar {
    display: flex;
    background: var(--white-color);
    border-radius: 50px;
    padding: 8px;
    box-shadow: 0 8px 32px rgba(0,0,0,0.1);
    max-width: 800px;
    margin: 0 auto;
}

.search-field {
    flex: 1;
    position: relative;
}

.search-field:not(:last-child) {
    border-right: 1px solid var(--border-color);
}

.search-field label {
    position: absolute;
    top: 8px;
    left: 15px;
    font-size: 0.75rem;
    color: var(--text-color);
    font-weight: 500;
    pointer-events: none;
}

.search-field input {
    width: 100%;
    padding: 28px 15px 8px 15px;
    border: none;
    outline: none;
    font-size: 0.95rem;
    color: var(--text-color);
    background: transparent;
}

.search-field input::placeholder {
    color: transparent;
}

.btn-search {
    background: var(--primary-color);
    color: var(--white-color);
    border: none;
    border-radius: 50%;
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    margin: 4px;
}

.btn-search:hover {
    background: var(--accent-color);
    transform: scale(1.05);
}

/* Popular Accommodations Section */
.popular-accommodations {
    padding: 5rem 0;
    background: var(--white-color);
}

.popular-accommodations h2 {
    text-align: center;
    font-size: 2.5rem;
    font-weight: 700;
    color: var(--dark-green);
    margin-bottom: 3rem;
}

.accommodations-container {
    min-height: 400px;
}

/* Loading State */
.loading-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4rem 2rem;
    text-align: center;
}

.spinner {
    width: 50px;
    height: 50px;
    border: 4px solid var(--light-green);
    border-top: 4px solid var(--primary-color);
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 1.5rem;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Error State */
.error-state {
    text-align: center;
    padding: 4rem 2rem;
    color: var(--danger-color);
}

.error-state i {
    font-size: 4rem;
    margin-bottom: 1.5rem;
}

.error-state h3 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
}

.error-state p {
    margin-bottom: 2rem;
    color: var(--text-color);
}

/* Empty State */
.empty-state {
    text-align: center;
    padding: 4rem 2rem;
    color: var(--text-color);
}

.empty-state i {
    font-size: 4rem;
    color: var(--light-green);
    margin-bottom: 1.5rem;
}

.empty-state h3 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: var(--dark-green);
}

/* Accommodations Grid */
.accommodations-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: 2rem;
}

.accommodation-card {
    background: var(--white-color);
    border-radius: 16px;
    overflow: hidden;
    box-shadow: var(--card-shadow);
    transition: all 0.3s ease;
    cursor: pointer;
}

.accommodation-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 12px 40px rgba(46, 139, 87, 0.15);
}

.card-image {
    position: relative;
    height: 220px;
    overflow: hidden;
}

.card-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.accommodation-card:hover .card-image img {
    transform: scale(1.05);
}

.favorite-icon {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: rgba(255,255,255,0.9);
    border-radius: 50%;
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.favorite-icon:hover {
    background: var(--white-color);
    transform: scale(1.1);
}

.favorite-icon i {
    color: var(--danger-color);
    font-size: 1.1rem;
}

.card-info {
    padding: 1.5rem;
}

.card-info h3 {
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--dark-green);
    margin-bottom: 0.8rem;
    line-height: 1.3;
}

.location {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: var(--text-color);
    margin-bottom: 1rem;
    font-size: 0.95rem;
}

.location i {
    color: var(--primary-color);
    font-size: 0.9rem;
}

.rating {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 1rem;
    font-size: 0.9rem;
    color: var(--text-color);
}

.rating i {
    color: #FFD700;
    font-size: 0.9rem;
}

.price {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--primary-color);
    margin: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
    .hero {
        height: 60vh;
        min-height: 400px;
    }
    
    .hero-content h1 {
        font-size: 2.5rem;
    }
    
    .hero-content p {
        font-size: 1.1rem;
    }
    
    .search-bar {
        flex-direction: column;
        border-radius: 16px;
        padding: 1rem;
    }
    
    .search-field:not(:last-child) {
        border-right: none;
        border-bottom: 1px solid var(--border-color);
        margin-bottom: 0.5rem;
    }
    
    .search-field input {
        padding: 12px 15px 12px 15px;
    }
    
    .search-field label {
        position: static;
        margin-bottom: 0.5rem;
        font-size: 0.85rem;
    }
    
    .btn-search {
        width: 100%;
        height: 48px;
        border-radius: 12px;
        margin-top: 1rem;
    }
    
    .accommodations-grid {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
    
    .popular-accommodations h2 {
        font-size: 2rem;
    }
}

@media (max-width: 480px) {
    .hero-content h1 {
        font-size: 2rem;
    }
    
    .hero-content p {
        font-size: 1rem;
    }
    
    .card-info {
        padding: 1rem;
    }
    
    .card-info h3 {
        font-size: 1.1rem;
    }
}
